body {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 700;
}

.navbar-brand {
  margin-left: 15px;
}

.navbar-right {
  justify-content: flex-end;
  display: flex !important;
  flex-basis: auto;
  flex-grow: 1;
  align-items: center;
}

.navbar-search {
  margin-left: 20px;
  width: 500px;
}

.landing-background {
  height: 100vh;
  padding-bottom: 2rem;
  background-color: #343a40;
  background: url("../images/landing-bg.jpg") no-repeat top center;
  background-size: cover;
  display: flex;
  align-items: center;
}

.landing-background .overlay {
  position: absolute;
  background-color: #212529;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0.3;
}

.spinner-container {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

@media (min-width: 768px) {
  .mobile-only {
    display: none;
  }

  .desktop-only {
    display: block;
  }
}

@media (max-width: 767px) {
  .mobile-only {
    display: block;
  }

  .desktop-only {
    display: none;
  }
}
